@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;700&family=Noto+Sans:wght@300;400;700&display=swap');

/*tag reset*/
body, h1, h2, h3, h4, h5, h6, p, ul, button, input[type="text"]{margin: 0;padding: 0;}
h1, h2, h3, h4, h5, h6{font-size: 100%;}
ul{list-style: none;}
a:link, a:visited, a:hover, a:active{text-decoration: none;color: inherit;}
img{vertical-align: top;}
img, video{max-width: 100%;}
button{border: none;background: none;cursor: pointer;}
input[type="text"]{border: 1px solid #aaa;border-radius: 0;}
button, input{font-family: inherit;font-size: inherit;color: inherit;}
address{font-style: normal;}
header, nav, section, footer{display: block;}
body{
    font-family: 'Noto Sans', 'Noto Sans KR', sans-serif;
    background-color: #f5f5f6;
}
.header{
    height: 80px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ddd;
    background-color: #ffffff;
}
.logo{
    padding: 0 40px;
    font-weight: 500;
    font-size: 25px;
}
.list{
    background-color: #ffffff;
    margin: 10px 20px 0 20px;
    border: 1px solid #ddd;
    padding: 20px;
}
.title_wrap{
    padding-bottom: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.title{
    display: inline-block;
    font-weight: 500;
}
.title_wrap .left > span{
    margin-left: 15px;
    cursor: pointer;
}
.title_wrap .left > span img{
    width: 18px;
    height: 18px;
    vertical-align: sub;
}
.title_wrap .left > span span{
    margin-left: 3px;
}

.title_wrap strong{
    font-size: 13px;
    font-weight: 400;
    text-align: right;
    color: #919191;
}

.btn_delete{
    padding: 2px 5px;
    background-color: #7e65ff;
    color: #ffffff;
    font-size: 12px;
    margin-left: 20px;
    font-weight: 600;
}

.btn{
    text-align: center;
    margin-top: 30px;
}
.btn input{
    outline: none;
    border: 1px solid #dddddd;
    width: 200px;
    padding: 0 10px;
    height: 50px;
    margin-right: 10px;
}
.btn button{
    background-color: #7e65ff;
    color: #fff;
    height: 50px;
    padding: 0 5px;
}

.modal{
    background-color: #ffffff;
    margin: 10px 20px 0 20px;
    border: 2px solid #000;
    padding: 20px;
}
.modal h4{
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
    margin-bottom: 5px;
}
.modal .date{
    text-align: right;
    color: #919191;
    font-size: 13px;
}
.modal .cnt{
    padding-top: 8px;
}